import React from 'react';
import { Container, Form, Button, Row, Col, Card } from 'react-bootstrap';
import Swal from 'sweetalert2';

const Contact = () => {
  const onSubmit = async (event) => {
    event.preventDefault();
    const formData = new FormData(event.target);
    formData.append("access_key", "5a47e287-c82c-4638-a5a7-7a8eaac74ec8");
    const object = Object.fromEntries(formData);
    const json = JSON.stringify(object);
    
    const res = await fetch("https://api.web3forms.com/submit", {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
        Accept: "application/json"
      },
      body: json
    }).then((res) => res.json());

    if (res.success) {
      Swal.fire({
        title: "Succès!",
        text: "Le mail a été envoyé!",
        icon: "success"
      });
    }
  };

  return (
    <>
      <Container className="my-5">
        <h1 className="mb-4">Contactez-nous</h1>
        <br />
        <Form onSubmit={onSubmit} className="mb-5">
          <Form.Group className="mb-3" controlId="formName">
            <Form.Label>Nom</Form.Label>
            <Form.Control name="name" type="text" placeholder="Entrez votre nom" required />
          </Form.Group>
          <Form.Group className="mb-3" controlId="formEmail">
            <Form.Label>Adresse email</Form.Label>
            <Form.Control name="email" type="email" placeholder="Entrez votre email" required />
          </Form.Group>
          <Form.Group className="mb-3" controlId="formMessage">
            <Form.Label>Message</Form.Label>
            <Form.Control name="message" as="textarea" rows={3} placeholder="Entrez votre message" required/>
          </Form.Group>
          <Button variant="primary" type="submit">
            Envoyer
          </Button>
        </Form>
      </Container>

      <Container fluid className="my-5 p-0"  style={{ backgroundColor: '#F1F2FA' }}>

        <Container className="my-5">
          <Row className="mb-5">
            <Col md={7}>
              <br />
              <br />
              <h2>Où nous trouver</h2>
              <hr className="border-3 border-primary opacity-50" />
            </Col>
          </Row>

          <Row>
            {/* Left column with info */}
            <Col md={7}>
              <Card className="border-0 shadow-sm mb-4">
                <Card.Body>
                  <h2 className="h4 mb-4 text-primary">Réception</h2>

                  <p>
                  Tous·tes les visiteur·euse·s peuvent se présenter au laboratoire situé au troisième étage du 7077 Avenue du Parc. 
                  Pour y accéder, veuillez utiliser l'ascenseur ou les escaliers situés à côté de la clinique universitaire.
                  </p>
                  
                  <p>
                  Pour les participant·e·s aux études de recherche, un·e membre de l'équipe vous accueillera directement à la sortie de l'ascenseur/escalier du 3ème étage.
                  </p>

                </Card.Body>
              </Card>

              <Row>
                <Col md={6}>
                  <Card className="border-0 shadow-sm mb-4 h-100">
                    <Card.Body>
                      <h3 className="h4 mb-3 text-primary">Addresse</h3>
                      <p className="mb-1">7077 Avenue du Parc, 3ème étage</p>
                      <p>Montréal, QC H3N 1X7</p>

                      <h3 className="h4 mb-3 text-primary">Heures d'ouverture</h3>
                      <p className="mb-1">Lundi au vendredi</p>
                      <p>9h à 17h</p>
                    </Card.Body>
                  </Card>
                </Col>

                <Col md={6}>
                  <Card className="border-0 shadow-sm mb-4 h-100">
                    <Card.Body>
                      <h3 className="h4 mb-3 text-primary">Adresse email</h3>
                      <p className="mb-4">labo-langage@gmail.com</p>

                      <h3 className="h4 mb-3 text-primary">Téléphone</h3>
                      <p className="mb-0">+1 000 000 0000</p>
                    </Card.Body>
                  </Card>
                </Col>
              </Row>
            </Col>

            {/* Right column with map */}
            <Col md={5} className="d-flex align-items-center">
              <div className="ratio ratio-1x1 h-60">
                <iframe 
                  src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d2795.4893681358457!2d-73.62645372346183!3d45.53251257107633!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x4cc919578b9df4b3%3A0x3e6591249408762c!2s7077%20Av.%20du%20Parc%2C%20Montr%C3%A9al%2C%20QC%20H3N%201X7!5e0!3m2!1sen!2sca!4v1702601548447!5m2!1sen!2sca"
                  className="border-0"
                  allowFullScreen=""
                  loading="lazy"
                  referrerPolicy="no-referrer-when-downgrade"
                  title="Location map"
                ></iframe>
              </div>
            </Col>
          </Row>

          
        </Container>
        <br />
      </Container>
    </>
  );
};

export default Contact;